.logo {
	height: 64px;
	width: 64px;
}

.nav {
	font-size: 18px;
	padding-left: 25px;
}

.nav a {
	margin-right: 25px;
	text-decoration: none;
	color: var(--text-color);
	-webkit-transition: color .2s;
	-o-transition: color .2s;
	transition: color .2s;
}

.nav a:hover {
	color: var(--highlight);
}

.nav>a.active {
	color: var(--highlight);
}

.user-nav {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	-ms-flex-align: center;
	    align-items: center;
}

.user-nav > .item {
	margin-left: 25px;
}



